  font-size: 18px;
  padding: 8px 16px;
}
.items-content{
  margin: 20px auto;
  gap: 10px;
}

.items-card, .characters-card{
  gap: 8px 0;
  border-radius: 10px;
}
.characters-card{
  padding: 8px 10px;
}
.items-card{
  padding: 8px;
}
.items-card img{
  width: 60px;
}
.characters-card img{
  border-radius: 10px;
  width: 90px;
}


